import React from 'react'
import './Register.css';

class Register extends React.Component {

	constructor(props){
		super(props);
		this.state = {
			name: '',
			email: '',
			password: '',
			isInvalid: false
		}
	}

	onNameChange = (event) => {
		this.setState({name: event.target.value})
	}

	onEmailChange = (event) => {
		this.setState({email: event.target.value})
	}

	onPasswordChange = (event) => {
		this.setState({password: event.target.value})
	}

	onSubmitRegister = () => {
		fetch('http://localhost:3000/register', {
			method: 'post',
			headers: {'Content-Type':'application/json'},
			body: JSON.stringify({
				name: this.state.name,
				email: this.state.email,
				password: this.state.password
			})
		})
			.then(response => response.json())
			.then(user => {
				if (user.id){
					this.props.loadUser(user)
					this.props.onRouteChange('home')
				}else{
					this.setState({isInvalid: true})
				}
			})

			//ADD TOKEN GENERATION AND SET AFTER REGISTER
		
	}

	render(){

		return (
		<article className="br3 ba dark-gray b--black-10 mv4 w-100 w-50-m w-25-l mw6 shadow-5 center bg-white-40">
			<main className="pa4 black-80">
			  <div className="measure">
			    <fieldset id="sign_up" className="ba b--transparent ph0 mh0">
			      <legend className="f1 fw6 ph0 mh0">Register</legend>
			      <div className="mt3">
			        <label className="db fw6 lh-copy f6" htmlFor="name">Name</label>
			        <input 
			        	className="b--black pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100" 
			        	type="text" 
			        	name="name"  
			        	id="name" 
			        	onChange = {this.onNameChange}
			        />
			      </div>
			      <div className="mt3">
			        <label className="db fw6 lh-copy f6" htmlFor="email-address">Email</label>
			        <input 
			        	className="b--black pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100" 
			        	type="email" 
			        	name="email-address"  
			        	id="email-address" 
			        	onChange = {this.onEmailChange}
			        />
			      </div>
			      <div className="mv3">
			        <label className="db fw6 lh-copy f6" htmlFor="password">Password</label>
			        <input 
			        	className="b--black pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100" 
			        	type="password" 
			        	name="password"  
			        	id="password"
			        	onChange = {this.onPasswordChange}
			        />
			      </div>
			    </fieldset>

			    { this.state.isInvalid &&
			    	<div className="f6 red mv3"> Incorrect Form Submission </div>
			    }

			    <div className="">
			      <input
			      		onClick = {this.onSubmitRegister}
			      		className="b ph3 pv2 input-reset ba b--black bg-transparent grow pointer f6 dib
			      		hover-white hover-bg-black" 
			      		type="submit"
			      		value="Register" />
			    </div>
			  </div>
			</main>
		</article>
		);
	}
	
}

export default Register;